<h1 id="report-writing" tabindex="-1"><a class="header-anchor" href="#report-writing" aria-hidden="true">#</a> Report Writing</h1>
<p>There are some common mistakes students do the first time they try to write a report describing the project work they've done. In this lecture some of those mistakes are pointed out so you can avoid doing them yourself. But first some important theory to know before you start writing your report.</p>
<h2 id="what-is-a-project" tabindex="-1"><a class="header-anchor" href="#what-is-a-project" aria-hidden="true">#</a> What is a project?</h2>
<p>Most reports are associated with a project, so you must have a good understanding of what a project is before you write a report on one. <a href="https://en.wikipedia.org/wiki/Project" target="_blank" rel="noopener noreferrer">Wikipedia's Project page<OutboundLink/></a> has a long definition of the term, but let us only focus on projects where the goal is to implement some software. A project is always about a problem that should be solved, and the solution (in this case the software) you create should solve that problem.</p>
<h3 id="the-problem" tabindex="-1"><a class="header-anchor" href="#the-problem" aria-hidden="true">#</a> The problem</h3>
<p>The goal with the software you create is to get people to use it (no point in creating something no one will use). Why would they use it? Because it will help them in some way. So the first thing you should decide in a project work is: <em>What is the problem people are having that we will try to solve in this project</em>? Examples of problems are:</p>
<ul>
<li>Crossing the road is dangerous.</li>
<li>Waiting (in line, at a bus stop, at the traffic lights, etc.) is boring AND a waste of time (two problems in one).</li>
<li>Leaving the children home alone is dangerous (parents are worried about both the house and their children).</li>
<li>Communicating with someone not speaking your language is hard.</li>
<li>Remembering when Arsenal plays their next game is hard, and it changes after each game.</li>
<li>Contacting all players in a hockey team (to notify them of cancelled matches, changed practice hours, etc.) is hard/time consuming.</li>
<li>Knowing what is cheap and what is expensive in a grocery store is hard.</li>
</ul>
<p>If you can't come up with a problem on your own, simply ask a friend or a company about problems they're having, and then you can try to solve that problem as your project.</p>
<p>When you have identified the problem to solve, you need to analyze the problem in depth to get a better understanding of it, so you know things like:</p>
<ul>
<li>Who are having the problem?</li>
<li>What is characteristic of those having the problem? E.g.:
<ul>
<li>Are they rich?</li>
<li>Do they live in big cities?</li>
<li>Are they old?</li>
<li>Do they all have driver licenses?</li>
<li>How much time are they willing to invest to solve the problem? (e.g. 15 minutes to learn how to use a new app?)</li>
<li>Etc.</li>
</ul>
</li>
<li>When are they having the problem?</li>
<li>How does the problem impact the rest of their lives?</li>
<li>Etc.</li>
</ul>
<p>Analyzing the problem in depth could be a big project itself taking many months, but let us keep things minimal here (you don't have time to do a proper analyze of the project in depth for the project you do in school) and move on.</p>
<h3 id="the-solution" tabindex="-1"><a class="header-anchor" href="#the-solution" aria-hidden="true">#</a> The solution</h3>
<p>When you have a good understanding of the problem, you need to come up with a solution to the problem. What the solution looks like do of course depends on what the problem is, but in our case the solution will always involve the creation of software. For example, if the problem is <em>My children has grown up and now I have a lot of small clothes no one in the family can use</em>, then maybe the solution could be to create a website where people can sell their clothes online to others.</p>
<p>Often you don't only come up with one solution, but many different solutions (coming up with different solutions could be a project itself taking many months), and then you continue to work with the solution you think is the most promising one. When you do, you need to be sure that the people having the problem really think that the solution you pick is a good solution they are willing to use (optimally willing to pay money for ðŸ¤‘). You can compare your solution to existing solutions to see if your solution seems to be better. For example, parents might now throw/give away their children clothes for free now, but with your solution they would get some money for the clothes, making them prefer your solution. Making a survey and asking the people having the problem if they think your solution is a good idea is of course a very smart thing to do.</p>
<p>When you have decided which solution to go with, you need to realize it (in the previous example implement the website). When you're done realizing the solution, we'll for simplicity consider the project to be over. Real projects could however continue after this. For example, the following could also be part of the project:</p>
<ul>
<li>Delivering the solution to a customer.</li>
<li>Running the solution for a customer.</li>
<li>Maintaining the solution for a customer.</li>
<li>Add new functionality to the solution for a customer.</li>
<li>Fix bugs in the solution.</li>
<li>...</li>
</ul>
<h2 id="report-structure" tabindex="-1"><a class="header-anchor" href="#report-structure" aria-hidden="true">#</a> Report structure</h2>
<p>Now that you have a basic understanding of what a project is, let's take a look at how we can write a report about one.</p>
<p>Reports describing projects tends to be quite long. Therefore we divide them into chapters, making them a bit more structured. But which chapters should we have? In which order should they be? There's no single correct answer to this, but let's go through an example of a good structure for a report.</p>
<h3 id="chapter-1-introduction" tabindex="-1"><a class="header-anchor" href="#chapter-1-introduction" aria-hidden="true">#</a> Chapter 1: Introduction</h3>
<p>The first chapter in the report should introduce the reader to the project. The reader does so far not know anything about your project, so the first thing you need to explain to the reader is which problem you try to solve and how you intend to solve it.</p>
<p>In this chapter you can think of the reader as an investor with a lot of money, and your goal is to convince the investor that the problem is a serious problem worth solving, and that your solution solves the problem in an excellent way. If you manage to convince the investor of this, she might be willing to invest some money in your project, and you get the money you need to start working on the project and to build the solution.</p>
<p>In our case the solution is a website, so we need to tell the reader what problem the users of the website are having and then explain how they solve that problem by using the website. A UML use-case diagram is an excellent tool to visualize what the user will be able to do on the website.</p>
<div class="custom-container tip"><p class="custom-container-title">Keep it simple!</p>
<p>Investors are not technical people (just rich and stupid bastards ðŸ˜‰), so avoid using technical terms. Anyone reading your report should be able to understand all the text in the Introduction chapter, even your own grandpa who doesn't even know how to turn off the radio.</p>
</div>
<h3 id="chapter-2-solution" tabindex="-1"><a class="header-anchor" href="#chapter-2-solution" aria-hidden="true">#</a> Chapter 2: Solution</h3>
<p>So, you have decided on a solution and you've got the money you need to build it from an investor? Great, now we just need to figure out the technical details of how to build the solution. The solution is usually quite complex, so dividing the solution into multiple chapters is usually a good idea, but if the solution is small and simple, a single chapter describing the solution can be enough.</p>
<p>In our case, the solution is a website, so this chapter could be about giving an overview of how we will implement the website. In our case we could explain that a user will use her web browser on her computer to communicate with our web application running on our server, and our server in turn will communicate with our database that runs on yet another server.</p>
<p>In our case the solution consists of two components we create ourselves:</p>
<ul>
<li>The web application</li>
<li>The database</li>
</ul>
<p>We have a lot to say about these, but that will be implementation details, and those are probably better described in chapters dedicated to describe these in detail, so this chapter is just about the architecture of the solution (an overview of the solution). This way, readers who don't know much about programming should still be able to read and understand most parts of this chapters.</p>
<div class="custom-container tip"><p class="custom-container-title">Tips!</p>
<p>In our case we describe the architecture of our solution in this chapter, so maybe <em>Architecture</em> is a more accurate chapter name than <em>Solution</em>? It's your report, you decide!</p>
</div>
<div class="custom-container tip"><p class="custom-container-title">Keep it simple!</p>
<p>In the Solution chapter you have to get a little bit more technical compared to the Introduction chapter, but try to keep it simple (avoid mentioning technical implementation details unless it makes sense to mention them here) so most readers can read the chapter and understand the solution overall without being technical experts. Your grandpa should be able to understand most of what you've written, even though <a href="https://www.youtube.com/watch?v=st6-DgWeuos" target="_blank" rel="noopener noreferrer">he doesn't know where the any key is<OutboundLink/></a>.</p>
</div>
<h3 id="database" tabindex="-1"><a class="header-anchor" href="#database" aria-hidden="true">#</a> Database</h3>
<p>We can use a separate chapter to describe the database component in our solution, so in this chapter we describe everything there is to know about the database, such as:</p>
<ul>
<li>What database do we use (MySQL? SQLite?, MariaDB? Etc.)</li>
<li>What tables do we have in the database?</li>
<li>What columns do we have in the tables?</li>
<li>What constraints do we have in our database?</li>
<li>...</li>
</ul>
<div class="custom-container tip"><p class="custom-container-title">Tips!</p>
<p>In this chapter you can expect the reader to know a lot about databases, so don't expect your grandpa to understand much of what you write here. This chapter will primarily be read by other developers working on the project to learn how they should use the database, and by quality assurance people (your boss, your team's quality assurance member, your customer's quality assurance checker, etc.) to verify that your solution has been implemented in a good way.</p>
</div>
<div class="custom-container warning"><p class="custom-container-title">Remember!</p>
<p>This chapter is about the database only, so no need to mention anything about the web application in this chapter.</p>
</div>
<h3 id="web-application" tabindex="-1"><a class="header-anchor" href="#web-application" aria-hidden="true">#</a> Web Application</h3>
<p>We can use a separate chapter to describe the web application component in our solution, so in this chapter we describe everything there is to know about the web application, such as:</p>
<ul>
<li>Which framework is the web application implemented in?</li>
<li>How is the source code structured?</li>
<li>How is authentication and authorization handled?</li>
<li>How is information about the clients remembered? E.g. cookies, sessions, tokens, etc.</li>
<li>Which security vulnerabilities has been thought of and is protected against?</li>
<li>Which libraries is the web application using?</li>
<li>...</li>
</ul>
<div class="custom-container tip"><p class="custom-container-title">Tips!</p>
<p>In this chapter you can expect the reader to know a lot about how web applications work, so don't expect your grandpa to understand much of what you write here. This chapter will primarily be read by other developers working on the project to learn how they should add new features to the web application, and by quality assurance people (your boss, your team's quality assurance member, your customer's quality assurance checker, etc.) to verify that your solution has been implemented in a good way.</p>
</div>
<div class="custom-container warning"><p class="custom-container-title">Remember!</p>
<p>This chapter is about the web application only, so no need to describe implementation details of the database. The only thing you need to mention about the database in this chapter is how you communicate with it/connect to it.</p>
</div>
<h3 id="other-chapters" tabindex="-1"><a class="header-anchor" href="#other-chapters" aria-hidden="true">#</a> Other chapters</h3>
<p>Add whichever other chapters you want that make sense to have. Maybe a chapter describing the website's graphical user interface is good to have? Or maybe designing the graphical user interface is an entire project on its own? Maybe the solution also includes a smartphone app? Then having a chapter explaining the implementation of that app would be good? Or maybe the smartphone app should be described in a separate report? Maybe our solution also includes special days people can come to a house and leave the clothes they want to sell there, and then others can come to the house and buy them? Then a chapter explaining how this works would be good, etc.</p>
<h2 id="readers-are-stupid" tabindex="-1"><a class="header-anchor" href="#readers-are-stupid" aria-hidden="true">#</a> Readers are stupid!</h2>
<p>When you write a report you must always remember that you write it for the readers. Each time you hesitate over something, such as:</p>
<ul>
<li>Which chapters do I need?</li>
<li>In which chapter should I write this?</li>
<li>Do I need a figure to explain this?</li>
<li>Have I explained this good enough now?</li>
</ul>
<p>Always think of the reader. Pretend you are a very stupid reader of the report, and that you don't know anything about the project. Then try to answer your question, and then go with the answer you come up with.</p>
<div class="custom-container tip"><p class="custom-container-title">Your opinion about the report doesn't matter</p>
<p>It doesn't matter whether you think the report is good or bad, what's important is what the readers think of your report, since they are the ones who will read it. To make them think the report is good, you often need to do more work on it than you initially think is necessary, because you know YOUR project work by heart, but the reader doesn't know anything about it, so you need to explain more things than you think.</p>
</div>
<h2 id="be-explicit" tabindex="-1"><a class="header-anchor" href="#be-explicit" aria-hidden="true">#</a> Be explicit</h2>
<p>If you write something that can be interpreted in different ways, readers have no idea which way that is the correct interpretation, since they don't have anyone to ask for clarification. So, when it comes to written text, it's very important to be explicit about what you mean, so there's no way for readers to interpret the text in different ways.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Let's take a look at an example from a report describing how a blog on an Android app works:</p>
<blockquote>
<p>It doesn't work to create empty blogposts.</p>
</blockquote>
<p>The problems with that wording are:</p>
<ol>
<li>There's no explanation of what <em>doesn't work</em> means. The app doesn't contain that functionality at all? Or the app crashes when that functionality is used? Or does nothing happen when one click on the create-button? Or is an error message shown to the user?</li>
<li>There's no explanation of what <em>empty</em> means. Missing all of title, content and date? Or just missing title? Or what?</li>
</ol>
<p>As you see, sentences in a report that are that vague are more confusing than helpful to the reader!</p>
<p>Let's take a look at a better worded sentence:</p>
<blockquote>
<p>When creating a new blogpost with an empty content, an error message is shown to the user indicating that new blogposts must have some content.</p>
</blockquote>
<p>This is much better! Everyone reading this sentence will get the very same understanding of how the app works. But most likely, you will have more validation rules than just that one, and you need to describe all of them. Then you should not describe each of them in separate sentences like that, but rather show them in a list, or a table, as in the example below.</p>
<blockquote>
<p>When creating a new blogposts, the following validation rules are applied:</p>
<ul>
<li>The content of the blogpost must not be empty</li>
<li>The content of the blogpost must at most consist of 1000 characters</li>
<li>The title of the blogpost must consist of at least 5 characters</li>
<li>The title of the blogpost must at most consist of 75 characters</li>
<li>The publication date can not be set to a date in the past</li>
</ul>
<p>If some of the rules are not followed, they will be shown as error messages to the user.</p>
</blockquote>
<p>This is a very good way of describing how it works;</p>
<ul>
<li>Lists are used, so it's easy to see where all the validation rules are specified, and to count how many they are, etc.</li>
<li>It's very easy to add more validation rules in the future: just add a rule to the list</li>
</ul>
<p>Even if your app only has a single validation rule, displaying that one in a list is still a very good idea, because it gives the report a very good structure, and it's very easy to add more validation rules to the report in the future.</p>
</div>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Another example from a report on a website:</p>
<blockquote>
<p>The client communicates with the server, and then it crashes.</p>
</blockquote>
<p>What crashes? The client? The server? The network? Or everything? Students love overusing the word <em>it</em>, but readers hate, because they often don't know what <em>it</em> refers to. It is much better to be explicit:</p>
<blockquote>
<p>The client communicates with the server, and then the client crashes.</p>
</blockquote>
<p>This is much better, now all readers gets the understanding that it's the client that crashes. But <em>then</em> in the sentence is still ambiguous. WHEN is <em>then</em>?</p>
<ul>
<li>then = Directly after the request has been sent?</li>
<li>then = While waiting for the response?</li>
<li>then = When the response is being received?</li>
<li>then = After the response has been received?</li>
</ul>
<p>Different readers will make different guesses here, and get different understandings. It's much better to be explicit than vague, for example:</p>
<blockquote>
<p>The client sends an HTTP request to the server, and when the client receives the HTTP response from the server the client crashes.</p>
</blockquote>
<p>This sentence is explicit all the way, so it's very easy to read and understand, and everybody gets the same understanding from it.</p>
</div>
<h2 id="common-student-mistakes" tabindex="-1"><a class="header-anchor" href="#common-student-mistakes" aria-hidden="true">#</a> Common student mistakes</h2>
<p>So, which are the common mistakes students do in their reports and that you should avoid? Here are some of them.</p>
<h3 id="non-unique-solution-name" tabindex="-1"><a class="header-anchor" href="#non-unique-solution-name" aria-hidden="true">#</a> Non unique solution name</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>My Website</p>
</template>
<template v-slot:example-1-good>
<p>ShoeSell</p>
</template>
<template v-slot:example-2-bad>
<p>Camera App</p>
</template>
<template v-slot:example-2-good>
<p>CamAppo</p>
</template>
<template v-slot:mistake>
<p>The name of the solution is very general.</p>
</template>
<template v-slot:problem>
<p>The solution is hard to find for people searching on the web, and it's hard to talk about the solution (e.g. Person A: <em>I like the Camera App, it's a great application</em>, Person B: <em>ðŸ˜• Which camera app?</em>).</p>
</template>
<template v-slot:solution>
<p>Use a more unique name on your solution. Compare to <em>Google</em>, <em>Netflix</em>, <em>Spotify</em>, etc. Hard to come up with a unique name? Use a <a href="https://www.google.se/search?q=project+name+generator" target="_blank" rel="noopener noreferrer">project name generator<OutboundLink/></a>.</p>
</template>
</ReportMistake>
<h3 id="weird-solution-name" tabindex="-1"><a class="header-anchor" href="#weird-solution-name" aria-hidden="true">#</a> Weird solution name</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>Jukioperlatino</p>
</template>
<template v-slot:example-1-good>
<p>Fnelly</p>
</template>
<template v-slot:example-2-bad>
<p>dsfkjs</p>
</template>
<template v-slot:example-2-good>
<p>Klanga</p>
</template>
<template v-slot:mistake>
<p>The name of the solution is too strange.</p>
</template>
<template v-slot:problem>
<p>People can't easily talk about the solution, and remembering the name is extremely hard.</p>
</template>
<template v-slot:solution>
<p>Use an easier (and possibly shorter) name. Compare to <em>Google</em>, <em>Netflix</em>, <em>Spotify</em>, etc..</p>
</template>
</ReportMistake>
<h3 id="components-used-before-defined" tabindex="-1"><a class="header-anchor" href="#components-used-before-defined" aria-hidden="true">#</a> Components used before defined</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>(first time <em>database</em> is mentioned in the report)</p>
<p>The database runs on a separate server.</p>
</template>
<template v-slot:example-1-good>
<p>(first time <em>database</em> is mentioned in the report)</p>
<p>The website has a database it uses to store the resources in.</p>
</template>
<template v-slot:mistake>
<p>The text refers to components before they have been introduced.</p>
</template>
<template v-slot:problem>
<p>The reader has no idea about what you refer to (which database <em>The database</em> in the example above refers to), nor that it's part of the solution.</p>
</template>
<template v-slot:solution>
<p>Introduce components before you refer to them.</p>
</template>
</ReportMistake>
<h3 id="ambiguous-explanations" tabindex="-1"><a class="header-anchor" href="#ambiguous-explanations" aria-hidden="true">#</a> Ambiguous explanations</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>The solution consists of a backend application and a frontend application. The application is...</p>
</template>
<template v-slot:example-1-good>
<p>The solution consists of a backend application and a frontend application. The frontend application is...</p>
</template>
<template v-slot:example-2-bad>
<p>Any user can login to the application and upload photos.</p>
</template>
<template v-slot:example-2-good>
<p>In the application any user can create and login to an account, and thereafter upload photos belonging to the account they logged in to.</p>
</template>
<template v-slot:mistake>
<p>The text is ambiguous, i.e. can be read in many different ways.</p>
</template>
<template v-slot:problem>
<p>The reader doesn't know what the text actually means and have to guess what the intended meaning is. <a href="https://www.xkcd.com/169/" target="_blank" rel="noopener noreferrer">It can end really bad for you.<OutboundLink/></a></p>
</template>
<template v-slot:solution>
<p>Be explicit about what you mean. Don't leave any room for different interpretations anywhere in the report.</p>
</template>
</ReportMistake>
<h3 id="inconsistent-terms" tabindex="-1"><a class="header-anchor" href="#inconsistent-terms" aria-hidden="true">#</a> Inconsistent terms</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>The solution consists of an Android application. The app is designed... The software securely communicates with...</p>
</template>
<template v-slot:example-1-good>
<p>The solution consists of an Android application. The application is designed... The application securely comminutes with...</p>
</template>
<template v-slot:example-2-bad>
<p><em>BikeIT</em> is a website where you can buy a hi-tech bike. On the site you can also sell your old bike.</p>
</template>
<template v-slot:example-2-good>
<p><em>BikeIT</em> is a website where you can buy a hi-tech bike. On the website you can also sell your old bike.</p>
</template>
<template v-slot:mistake>
<p>The text uses different terms to refer to the same thing.</p>
</template>
<template v-slot:problem>
<p>It's hard for the reader to read the text, and the reader gets unsure if you refer to the same thing or two different things when you refer to it using different terms.</p>
</template>
<template v-slot:solution>
<p>Be consistent with which terms you use.</p>
</template>
</ReportMistake>
<h3 id="making-text-stand-out" tabindex="-1"><a class="header-anchor" href="#making-text-stand-out" aria-hidden="true">#</a> Making text stand out</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>&quot;<b><i><u>Pangoga</u></i></b>&quot; is a platform that...</p>
</template>
<template v-slot:example-1-good>
<p><i>Pangoga</i> is a platform that...</p>
</template>
<template v-slot:mistake>
<p>Some of the text stands out too much.</p>
</template>
<template v-slot:problem>
<p>The text is harder to read when a word stands out too much from the rest of the text. For example, when you read this paragraph chances are you noticed this smiley ðŸ˜ƒ before you were supposed to, stealing your focus from the text. It can even steal your focus when you read this text, which appears after it.</p>
</template>
<template v-slot:solution>
<p>Don't make the text standout too much. Just using italic is enough in most cases. When showing inline code, making it standout like <code>this</code> is very common.</p>
</template>
</ReportMistake>
<h3 id="acronyms-without-explanations" tabindex="-1"><a class="header-anchor" href="#acronyms-without-explanations" aria-hidden="true">#</a> Acronyms without explanations</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>The GUI has been...</p>
</template>
<template v-slot:example-1-good>
<p>The Graphical User Interface (GUI) has been...</p>
</template>
<template v-slot:mistake>
<p>The text uses acronyms without explaining what they mean.</p>
</template>
<template v-slot:problem>
<p>The reader doesn't know what the acronym stands for and doesn't understand the text.</p>
</template>
<template v-slot:solution>
<p>Always explain what an acronym stands for the first time you use it in the report.</p>
</template>
</ReportMistake>
<h3 id="acronyms-are-introduced-wrong" tabindex="-1"><a class="header-anchor" href="#acronyms-are-introduced-wrong" aria-hidden="true">#</a> Acronyms are introduced wrong</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>The Graphical User Interface (from here on written as GUI) has been...</p>
</template>
<template v-slot:example-1-good>
<p>The Graphical User Interface (GUI) has been...</p>
</template>
<template v-slot:mistake>
<p>The standard notation for introducing acronyms has not been used.</p>
</template>
<template v-slot:problem>
<p>The standard notation is very short, if you use another notation the reader needs to do more work to read it. The reader also needs to learn your specific way of introducing acronyms, which is an extra effort for the reader.</p>
</template>
<template v-slot:solution>
<p>Stick to using the standard notation for introducing acronyms, or (second best) don't introduce them at all (if you do introduce an acronym, you have to use the acronym somewhere later in the text).</p>
</template>
</ReportMistake>
<h3 id="repeating-information" tabindex="-1"><a class="header-anchor" href="#repeating-information" aria-hidden="true">#</a> Repeating information</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>The database contains three tables: accounts, blogposts and comments. [...] The three type of resources in the database are accounts, blogposts and comments. [...] The resources stored in the database (accounts, blogposts and comments) are all validated before they are inserted.</p>
</template>
<template v-slot:example-1-good>
<p>The database contains three tables: accounts, blogposts and comments.</p>
</template>
<template v-slot:mistake>
<p>The same type of information is mentioned multiple times in the report, often across many different chapters.</p>
</template>
<template v-slot:problem>
<p>Making changes in the future will be hard, since the report needs to be updated at multiple places (and you will probably forget to update the report at all places, which makes it inconsistent). The report does also have a poor structure if the same information is described at multiple places. For example, if the user want to find this specific information and it's written at multiple different places, it is not obvious where in the report the user should start searching for it. Also, the reader doesn't get any new information by reading the same information multiple times, wasting the reader's time.</p>
</template>
<template v-slot:solution>
<p>Don't repeat the same information; each thing worth mentioning should only be mentioned once, and it should be obvious where in the report (which chapter) each thing is described. If it's not obvious you probably need to re-structure your report somehow.</p>
</template>
</ReportMistake>
<h3 id="chapters-start-with-figures" tabindex="-1"><a class="header-anchor" href="#chapters-start-with-figures" aria-hidden="true">#</a> Chapters start with figures</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>(in the beginning of a chapter)</p>
<p>[Actual Figure]</p>
<p>The figure above shows...</p>
</template>
<template v-slot:example-1-good>
<p>(in the beginning of a chapter)</p>
<p>The figure below shows...</p>
<p>[Actual Figure]</p>
</template>
<template v-slot:mistake>
<p>The first thing in the chapter is a figure.</p>
</template>
<template v-slot:problem>
<p>When the reader starts reading the chapter, she has no idea why there's a figure there, or why she should look at it.</p>
</template>
<template v-slot:solution>
<p>Always start a chapter with text, and if you want the reader to immediately look at a figure, tell the reader to do that the first thing you write in the chapter.</p>
</template>
</ReportMistake>
<h3 id="chapters-missing-figures" tabindex="-1"><a class="header-anchor" href="#chapters-missing-figures" aria-hidden="true">#</a> Chapters missing figures</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>The default profile avatar is a picture of a human. Only the head is shown, the human is almost smiling and has blue ring above the head.</p>
</template>
<template v-slot:example-1-good>
<p>The default profile avatar is shown in Figure 1 below.</p>
<p>ðŸ‘¼<br>
Figure 1: The default avatar profile.</p>
</template>
<template v-slot:mistake>
<p>No figures are used in a chapter; the chapter only consists of text.</p>
</template>
<template v-slot:problem>
<p>Figures are much easier to understand and faster to interpret than reading a long wall of text. It's also easier to remember a figure than it is to remember something described in text. It's also easier to have a discussion about a figure than it is about something only described in text. Each user reading a text creates their own image in their head about what they're reading, by using a figure all readers get the same picture in their head.</p>
</template>
<template v-slot:solution>
<p>Use many figures in your chapters.</p>
</template>
</ReportMistake>
<h3 id="figures-don-t-have-captions" tabindex="-1"><a class="header-anchor" href="#figures-don-t-have-captions" aria-hidden="true">#</a> Figures don't have captions</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>ðŸµ</p>
</template>
<template v-slot:example-1-good>
<p>ðŸµ<br>
Figure 1: Picture of Peter's pet DK.</p>
</template>
<template v-slot:mistake>
<p>Figures don't have captions.</p>
</template>
<template v-slot:problem>
<p>Without a descriptive caption, the reader is not always sure what the figure represents, nor why it's there.</p>
</template>
<template v-slot:solution>
<p>Each figure should always have a descriptive caption that explains what the figure shows, no matter how easy it is to interpret it.</p>
</template>
</ReportMistake>
<h3 id="captions-aren-t-descriptive" tabindex="-1"><a class="header-anchor" href="#captions-aren-t-descriptive" aria-hidden="true">#</a> Captions aren't descriptive</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>ðŸµ<br>
Figure 1: A monkey.</p>
</template>
<template v-slot:example-1-good>
<p>ðŸµ<br>
Figure 1: What students transforms to if they eat too many bananas (a monkey).</p>
</template>
<template v-slot:mistake>
<p>The caption doesn't describe the figure good enough.</p>
</template>
<template v-slot:problem>
<p>The user doesn't know why the figure is there, how it's relevant to the text nor how to interpret the figure.</p>
</template>
<template v-slot:solution>
<p>Use a descriptive caption. If the figure consists of components, colors, arrows, etc., describe what each component, color, arrow, etc. represent.</p>
</template>
</ReportMistake>
<h3 id="figures-don-t-have-figure-numbers" tabindex="-1"><a class="header-anchor" href="#figures-don-t-have-figure-numbers" aria-hidden="true">#</a> Figures don't have figure numbers</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>ðŸµ</p>
</template>
<template v-slot:example-1-good>
<p>ðŸµ<br>
Figure 1: What humans looked like just before we evolved from apes.</p>
</template>
<template v-slot:mistake>
<p>Figures don't have figures number.</p>
</template>
<template v-slot:problem>
<p>You can't refer to a specific figure from the text, and the reader can't easily send the report to a friend and ask the friend to look at a specific figure in the report.</p>
</template>
<template v-slot:solution>
<p>Give each figure a unique figure number.</p>
</template>
</ReportMistake>
<h3 id="figures-aren-t-referred-to-properly" tabindex="-1"><a class="header-anchor" href="#figures-aren-t-referred-to-properly" aria-hidden="true">#</a> Figures aren't referred to properly</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>Some text that doesn't mention the figure above/below at all...</p>
</template>
<template v-slot:example-1-good>
<p>Figure 5 below shows...</p>
</template>
<template v-slot:example-2-bad>
<p>The figure below shows...</p>
</template>
<template v-slot:example-2-good>
<p>Figure 5 below shows...</p>
</template>
<template v-slot:mistake>
<p>The text refers to figures without using their figure numbers.</p>
</template>
<template v-slot:problem>
<p>If you don't refer to the figure from the main text at all, it's unclear when and why the reader should look at the figure. If you refer to a figure without using the figure numbers it's often ambiguous which figure you refer to. For example, <em>the figure below</em> could refer to any of all the figures below the text, which are quite many in the beginning of the report.</p>
</template>
<template v-slot:solution>
<p>Always refer to figures using their figure numbers.</p>
</template>
</ReportMistake>
<h3 id="the-figure-is-far-away" tabindex="-1"><a class="header-anchor" href="#the-figure-is-far-away" aria-hidden="true">#</a> The figure is far away</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>(at the begging of the chapter)</p>
<p>Figure 4 at the end of the chapter shows...</p>
<p>(at the end of the chapter)</p>
<p>[Actual Figure 4]</p>
</template>
<template v-slot:example-1-good>
<p>(at the begging of the chapter)</p>
<p>Figure 4 below shows...</p>
<p>[Actual Figure 4]</p>
<p>...</p>
</template>
<template v-slot:mistake>
<p>The text refers to a figure, and the figure is far away.</p>
</template>
<template v-slot:problem>
<p>The reader needs to stop reading, scroll to the figure, interpret the figure, and then scroll back to the text. This is inconvenient, and the reader loses her concentration, making the report very hard to read.</p>
</template>
<template v-slot:solution>
<p>Place figures close to the text referring to the figure.</p>
</template>
</ReportMistake>
<h3 id="putting-the-figures-at-the-end" tabindex="-1"><a class="header-anchor" href="#putting-the-figures-at-the-end" aria-hidden="true">#</a> Putting the figures at the end</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>...</p>
<p>(at the end of the chapter)</p>
<p>Figure 8 below gives an overview of the architecture.</p>
<p>[Actual Figure 8]</p>
</template>
<template v-slot:example-1-good>
<p>(at the begging of the chapter)</p>
<p>Figure 8 below gives an overview of the architecture.</p>
<p>[Actual Figure 8]</p>
<p>...</p>
</template>
<template v-slot:mistake>
<p>The figures are put after most of the text, making the reader first read the text, and then look at the figures.</p>
</template>
<template v-slot:problem>
<p>Reading text is hard and takes time, interpreting figures is easy and can be done fast.</p>
</template>
<template v-slot:solution>
<p>Put figures (often) at the beginning of the text. They give a good understanding of the big picture, and then the reader can read the text to learn the details.</p>
</template>
</ReportMistake>
<h3 id="images-display-text" tabindex="-1"><a class="header-anchor" href="#images-display-text" aria-hidden="true">#</a> Images display text</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>Figure 5 below is a screenshoot of the getAllBlogposts() function.</p>
<p><img src="@source/lectures/report-writing/code-as-image.png" alt="Code in image"></p>
</template>
<template v-slot:example-1-good>
<p>Figure 5 below contains the code for the getAllBlogposts() function.</p>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>function getAllBlogposts(){
   ...
}
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div></template>
<template v-slot:mistake>
<p>Code (text in general) is shown as images/screenshots instead of text.</p>
</template>
<template v-slot:problem>
<p>Searching does not work, zooming works poorly (images have limited resolution) and copy-pasting the text doesn't work.</p>
</template>
<template v-slot:solution>
<p>Always display code (and text) as text, not as images.</p>
</template>
</ReportMistake>
<h3 id="use-contractions" tabindex="-1"><a class="header-anchor" href="#use-contractions" aria-hidden="true">#</a> Use contractions</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>If the client doesn't receive back a response within 5 seconds...</p>
</template>
<template v-slot:example-1-good>
<p>If the client does not receive back a response within 5 seconds...</p>
</template>
<template v-slot:mistake>
<p>Contractions (didn't, can't, won't, etc.) are used.</p>
</template>
<template v-slot:problem>
<p>Contractions should not be used in reports.</p>
</template>
<template v-slot:solution>
<p>Never use contractions in a report.</p>
</template>
</ReportMistake>
<h3 id="use-i-we-etc" tabindex="-1"><a class="header-anchor" href="#use-i-we-etc" aria-hidden="true">#</a> Use I, we, etc.</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>We use the database MySQL to...</p>
</template>
<template v-slot:example-1-good>
<p>A MySQL database is used to...</p>
</template>
<template v-slot:mistake>
<p>Words such as I, we, us, our, etc. are used.</p>
</template>
<template v-slot:problem>
<p>The reader doesn't know who <em>I</em>/<em>we</em>/etc. are. Also, the report should often describe a solution/product, and that should be independent of who created it, so there should be no need to use I/we in a report unless you express you own opinions/decisions. Also, if you write <i>This application is developed and maintained by me.</i>, then you need to update the report if you give away the project to someone else or if you invite someone else to work on the project with you.</p>
</template>
<template v-slot:solution>
<p>Avoid using I, we, etc. unless you are expressing your own opinions/decisions.</p>
</template>
</ReportMistake>
<h3 id="don-t-properly-mark-up-the-text" tabindex="-1"><a class="header-anchor" href="#don-t-properly-mark-up-the-text" aria-hidden="true">#</a> Don't properly mark up the text</h3>
<ReportMistake>
<template v-slot:mistake>
<p>A bold paragraph is used as a header.</p>
</template>
<template v-slot:problem>
<p>The header won't appear in the table of content, the reader gets unsure about whether the bold paragraph is a paragraph or a header. You also risk ending up with the bold paragraph at the end of one page, and the text belonging to it on the top of next page.</p>
</template>
<template v-slot:solution>
<p>Properly mark up the text: mark headers as headers, paragraphs as paragraphs, etc.</p>
</template>
</ReportMistake>
<h3 id="inconsistent-usage-of-white-space" tabindex="-1"><a class="header-anchor" href="#inconsistent-usage-of-white-space" aria-hidden="true">#</a> Inconsistent usage of white-space</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>First paragraph...<br><br>Second paragraph...<br>Third paragraph...</p>
</template>
<template v-slot:example-1-good>
<p>First paragraph...<br><br>Second paragraph...<br><br>Third paragraph...</p>
</template>
<template v-slot:mistake>
<p>Different parts of the report use white-space differently around headers, lists, paragraphs, tables, figures, etc.</p>
</template>
<template v-slot:problem>
<p>It's hard to see the structure of the report when white-space is used inconsistently.</p>
</template>
<template v-slot:solution>
<p>Be consistent with how you use white-space throughout the entire report.</p>
</template>
</ReportMistake>
<h3 id="describe-things-where-they-shouldn-t-be-described" tabindex="-1"><a class="header-anchor" href="#describe-things-where-they-shouldn-t-be-described" aria-hidden="true">#</a> Describe things where they shouldn't be described</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>The database chapter describes parts of the web application (e.g. how the web application communicates with the database, or how the source code in the web application that communicates with the database has been structured, etc.).</p>
</template>
<template v-slot:example-1-good>
<p>The database chapter only describes the database, the web application is not mentioned at all since the database doesn't use the web application.</p>
</template>
<template v-slot:mistake>
<p>One thing is described in a chapter where it doesn't make sense to describe it.</p>
</template>
<template v-slot:problem>
<p>When the reader is looking for some specific information, she won't find it in the chapter she expects to find it in. And when she wants to only learn about a specific thing by reading the corresponding chapter, she's also forced to read the unrelated thing you describe there.</p>
</template>
<template v-slot:solution>
<p>In a chapter, only describe the thing the chapter is about.</p>
</template>
</ReportMistake>
<h3 id="spelling-names-wrong" tabindex="-1"><a class="header-anchor" href="#spelling-names-wrong" aria-hidden="true">#</a> Spelling names wrong</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>The graphical user interface is implemented in html.</p>
</template>
<template v-slot:example-1-good>
<p>The graphical user interface is implemented in HTML.</p>
</template>
<template v-slot:example-2-bad>
<p>The framework express is used to implement the web application.</p>
</template>
<template v-slot:example-2-good>
<p>The framework Express is used to implement the web application.</p>
</template>
<template v-slot:mistake>
<p>The spelling of names (languages, frameworks, libraries, etc.) are wrong.</p>
</template>
<template v-slot:problem>
<p>It's harder for the reader to read the text, and the reader is unsure what you refer to.</p>
</template>
<template v-slot:solution>
<p>Use the correct spelling of names. Feel free to name your own things whatever you want, but be sure to use the correct names others have given their solutions.</p>
</template>
</ReportMistake>
<h3 id="describing-others-work" tabindex="-1"><a class="header-anchor" href="#describing-others-work" aria-hidden="true">#</a> Describing others work</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>The graphical user interface is implemented in HTML. HTML is a markup language that contains the tags &lt;a&gt;, &lt;br&gt;, &lt;p&gt;, &lt;link&gt;...</p>
</template>
<template v-slot:example-1-good>
<p>The graphical user interface is implemented in HTML<sup><a href="https://html.spec.whatwg.org/" target="_blank" rel="noopener noreferrer">1<OutboundLink/></a></sup>. HTML is a markup language used to define what different type of text represents, such as headers and paragraphs.</p>
</template>
<template v-slot:example-2-bad>
<p>The web browser communicates with the web application by sending HTTP requests to it. HTTP is a protocol built on top of TCP, which is a transfer protocol for sending small packages (messages) from one computer to another. ...</p>
</template>
<template v-slot:example-2-good>
<p>The web browser uses HTTP<sup><a href="https://tools.ietf.org/html/rfc2616" target="_blank" rel="noopener noreferrer">1<OutboundLink/></a></sup> to communicate with the web application. In HTTP, the client (the web browser) simply sends an HTTP request to the server (the web application), telling the server what it wants it to do, then the server do that and sends back an HTTP response.</p>
</template>
<template v-slot:mistake>
<p>Techniques used to implement the solution (for example HTTP, HTML, CSS, Express, etc.) has been described in detail.</p>
</template>
<template v-slot:problem>
<p>The report should be about the solution you have created, not details about the techniques you have used to implement it. Many readers do probably already know these techniques, and reading about them is no fun, and you describing them in vain wastes your time.</p>
</template>
<template v-slot:solution>
<p>Only describe what's unique to your solution. You can describe the techniques you are using (HTTP, HTML, etc.) very short and briefly, but for more information about these, provide references to their specifications/documentations and let the reader read more about them there instead. No use in you writing a tutorial about how other techniques work.</p>
</template>
</ReportMistake>
<h3 id="figures-are-referenced-after-the-figure" tabindex="-1"><a class="header-anchor" href="#figures-are-referenced-after-the-figure" aria-hidden="true">#</a> Figures are referenced after the figure</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>[...]</p>
<p>ðŸµðŸµ<br>
Figure 5: Picture of two twins (Bob to the left, David to the right).</p>
<p>Figure 5 above shows an example of two twins.</p>
<p>[...]</p>
</template>
<template v-slot:example-1-good>
<p>[...]</p>
<p>Figure 5 below shows an example of two twins.</p>
<p>ðŸµðŸµ<br>
Figure 5: Picture of two twins (Bob to the left, David to the right).</p>
<p>[...]</p>
</template>
<template v-slot:mistake>
<p>First a figure is shown to the reader, and then the reader is instructed to look at the figure.</p>
</template>
<template v-slot:problem>
<p>Humans read text from top to bottom. If the text below the figure refers to the figure, the reader will look at the figure before she has been instructed to look at it, and she has no idea why she looks at the figure. Then when she gets to the text below the figure, she needs to go back up to the figure again, breaking the readers &quot;from top to bottom&quot; reading flow.</p>
</template>
<template v-slot:solution>
<p>The text instructing the reader to look at the figure should always be placed above the figure.</p>
</template>
</ReportMistake>
<h3 id="chapter-titles-ending-with-colon" tabindex="-1"><a class="header-anchor" href="#chapter-titles-ending-with-colon" aria-hidden="true">#</a> Chapter titles ending with colon</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p><span style="font-weight: bold; font-size: 150%;">Web Application:</span></p>
<p>The web application...</p>
</template>
<template v-slot:example-1-good>
<p><span style="font-weight: bold; font-size: 150%;">Web Application</span></p>
<p>The web application...</p>
</template>
<template v-slot:mistake>
<p>Colon is written at the end of chapter titles.</p>
</template>
<template v-slot:problem>
<p>The standard way of writing headers is not followed, making it harder to read the text.</p>
</template>
<template v-slot:solution>
<p>Write headers the way headers are usually written; don't end chapter titles with colon.</p>
</template>
</ReportMistake>
<h3 id="paragraphs-consist-of-a-single-sentence" tabindex="-1"><a class="header-anchor" href="#paragraphs-consist-of-a-single-sentence" aria-hidden="true">#</a> Paragraphs consist of a single sentence</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>Users can create accounts on the website.</p>
<p>The primary color on the website is yellow.</p>
<p>Admins can see things ordinary users can't see.</p>
</template>
<template v-slot:example-1-good>
<p>Users can create accounts on the website. After a user has logged in to an account, the user can create blogposts belonging to that account. Newly created blogposts are not shown on the website by default, but must first be read and approved by an admin.</p>
<p>For the graphical user interface the primary color is yellow. That color was chosen because it reminds the user of the sun and gives the user a warm and nice feeling.</p>
<p>Admins can do some things ordinary users can't do on the website. They see and can use links and forms through which they get extra functionality, such as Delete button next to each blogpost they can click on to delete blogposts.</p>
</template>
<template v-slot:mistake>
<p>Many paragraphs consist of a single sentence.</p>
</template>
<template v-slot:problem>
<p>Paragraphs consisting of a single sentence are usually no good paragraphs. Compare to a sentence consisting of a single word, or a list consisting of a single list item. Most paragraphs should consist of multiple (related) sentences, otherwise your report won't have a good structure.</p>
</template>
<template v-slot:solution>
<p>Write headers the way headers are usually written; don't end chapter titles with colon.</p>
</template>
</ReportMistake>
<h3 id="images-missing-border" tabindex="-1"><a class="header-anchor" href="#images-missing-border" aria-hidden="true">#</a> Images missing border</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<img src="@source/lectures/report-writing/screenshot.png" alt="Screenshot of the website.">
<p>Is this text part of the figure? Where does the figure end?</p>
</template>
<template v-slot:example-1-good>
<img src="@source/lectures/report-writing/screenshot.png" alt="Screenshot of the website." style="border: 3px solid gray;">
<p>Is this text part of the figure? Obviously not.</p>
</template>
<template v-slot:mistake>
<p>Much of the image has the same color as the background of the report.</p>
</template>
<template v-slot:problem>
<p>It's hard to see where the image starts and ends.</p>
</template>
<template v-slot:solution>
<p>Give images consisting of the reports background color a border.</p>
</template>
</ReportMistake>
<h3 id="wall-of-text" tabindex="-1"><a class="header-anchor" href="#wall-of-text" aria-hidden="true">#</a> Wall of text</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<p>What happens when the user use the website is that the user first starts the web browser, then the web browser sends an HTTP request to the server. When the server receives the HTTP request, it carries out the request and generates and send back an HTTP response to the web browser. The HTTP response contains a webpage the web browser then displays.</p>
</template>
<template v-slot:example-1-good>
<p>What happens when the user use the website is that:</p>
<ol>
<li>The user starts the web browser.</li>
<li>The web browser sends an HTTP request to the server.</li>
<li>The server receives and carries out the HTTP request.</li>
<li>The server generates and sends back an HTTP response to the web browser.</li>
<li>The web browser displays the webpage in the HTTP response to the user.</li>
</ol>
</template>
<template v-slot:example-2-bad>
<p>What happens when the user use the website is that the user first starts the web browser, then the web browser sends an HTTP request to the server. When the server receives the HTTP request, it carries out the request and generates and send back an HTTP response to the web browser. The HTTP response contains a webpage the web browser then displays.</p>
</template>
<template v-slot:example-2-good>
<p>Figure 8 below shows what happens when the user uses the website.</p>
<RenderMermaid graph-definition="
sequenceDiagram
	participant User
	participant Web Browser
	participant Server
	User->>Web Browser: Start
	activate User
	activate Web Browser
	Web Browser->>Server: HTTP request
	activate Server
	Server->>Server: Carry out request
	Server-->>Web Browser: HTTP response
	deactivate Server
	Web Browser-->>User: Display webpage
	deactivate Web Browser
	deactivate User
" />
<p>Figure 8: What happens when the user uses the website.</p>
</template>
<template v-slot:mistake>
<p>The student tries to explain everything using only text, often in a gigantic paragraph.</p>
</template>
<template v-slot:problem>
<p>Of all the different ways one can describe how something works (text in paragraphs, lists, figures, tables, etc.), text in paragraphs is the hardest and slowest one to read.</p>
</template>
<template v-slot:solution>
<p>Try to explain as much as possible using lists, tables, figures, etc. Use paragraphs as a last resort.</p>
</template>
